@import (reference) "../../../styles/variables.less";

.pizi-menu-item{
    display: inline-flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    font-size: 1.4em;
    width: 50%;
    max-width: 200px;
    height: auto;
    color: inherit;
    padding: 1rem;
    text-decoration: none;

    &:active{
        color: inherit;
        filter: brightness(0.2);
    }

    &:hover{
        color: inherit;
        opacity: 0.7;
    }

    .logo{
        color: @color-secondary;
    }

    svg{
        font-size: 2.2em;
    }

    label{
        cursor: pointer;
        line-height: 1;
        text-align: center;
    }

    @import "menu-item-sm";
}