@import (reference) "../../../styles/variables.less";
@import (reference) "../../../styles/component-mixins.less";

.pizi-text-input{

    .pizi-text-input__input{

        input{
            background-color: inherit;
            border: none;
            border-bottom: @border;
            border-color: @color-grey;
            color: @color-white;
            font-size: 1.4em;
            border-width: 0.1em;
            width: 250px;

            &:placeholder-shown{
                opacity: 0.6;
                font-style: italic;
            }
        }
    }

    &.alt{
        label{
            color: fade(@color-secondary, 70%);
        }

        .pizi-text-input__input{
            input{
                color: @color-secondary;
                border-color: fade(@color-secondary, 25%);
            }
        }
    }

    &.error{
        input{
            border-color: @color-error;
        }
    }
}