/**********************************************
                    Headings
 **********************************************/
h1, h2, h3, h4, h5, h6{
    color: @color-main;
    margin-bottom: 0.4em;
    line-height: @line-height;
    border-width: 0.3rem;
    text-overflow: ellipsis;
    padding: 0;
}

h1{
    font-size: 2.6rem;
}

h2{
    font-size: 2.2rem;
}

h3{
    font-size: 1.8rem;
}

h4{
    font-size: 1.4rem;
}

h5{
    font-size: 1.2rem;
}

h6{
    font-size: @font-medium;
}

/**********************************************
                    Paragraph
 **********************************************/
p{
    padding: 0;
    text-indent: 2px;
    border-radius: @border-radius;
}

@import "typography-sm";