@import (reference) "../../../styles/variables.less";
@import (reference) "../../../styles/component-mixins.less";

.pizi-switch{
    
    .pizi-switch__input{
        width: 3em;
        height: 1.5em;
        border: @border;
        border-radius: @border-radius;
        position: relative;
        border-color: #888;
        margin-top: 0.4em;

        input{
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            opacity: 0;
            cursor: pointer;
            appearance: none;
            background-color: transparent;
        }

        .pizi-switch__input__checked{
            width: 1.5em;
            height: 1.5em;
            border: @border;
            border-radius: @border-radius;
            position: absolute;
            top: -@border-width;
            left: -@border-width;
            transition: width 0.1s linear;
            pointer-events: none;
            display: inline-flex;
            align-items: center;
            justify-content: flex-end;
            border-color: #888;
            background-color: #888;

            &:after{
                content: "";
                width: 1.2em;
                height: 1.2em;
                border-radius: 50px;
                display: inline-block;
                right: 0;
                background-color: #111;
            }
        }
    }
    &.checked{
        .pizi-switch__input__checked{
            width: 3em;
        }
    }
}